// src/components/CoverImage.js
import React from 'react';

const CoverImage = () => {
  return (
  <div style={{ 
    backgroundColor: '#fff', // background-color를 camelCase로 수정
    boxShadow: '0px 0px 8px rgba(0, 0, 0, 0.1)' // box-shadow를 camelCase로 수정
  }}>
    <div style={{position: 'relative'}}>
      <div style={{ textAlign: 'center', position: 'relative', height: '100vh',overflow: 'hidden' }}>
        <img
          alt="cover_image"
          fetchpriority="high" // 소문자로 변경
          decoding="async"
          data-nimg="fill"
          className="object-cover w-full h-full"
          src="/image_20241011_173757_384.jpeg" // public 폴더에서 이미지 경로
          style={{
            position: 'absolute',
            height: '100%',
            inset: '0px',
            color: 'transparent',
            overflow: 'hidden',
            left: '50%',
            transform: 'translateX(-50%)',
            width :  '100%'
          }}
        />
      </div>

      <div style={{ backgroundColor:'white', paddingBottom: '40px', paddingTop: '40px' }}>
      <p style={{ margin: '0', fontSize: '1.2rem' }}>
        성 기 덕 <span style={{ margin: '0 42px' }}>|</span> 노 승 혜
      </p>
      <p style={{ margin: '5px 0', fontSize: '.85rem', marginTop:'26px' }}>2025.02.22. SAT 16:30PM</p>
      <p style={{ margin: '0', fontSize: '.85rem' }}>메리빌리아 더 프리스티지 웨딩홀</p>
      </div>
      
      <div
        style={{
          position: 'absolute',
          width: 'calc(100% - 32px)',
          height: 'calc(100% - 32px)',
          top: '16px',
          left: '16px',
          border: '2px solid #EFEFEF',
        }}
      ></div>
    </div>
    </div>
  );
};

export default CoverImage;
