import React, { useEffect } from 'react';
import './KakaoMap.css'; // KakaoMap.css 파일을 가져옵니다.

const KakaoMap = () => {
    useEffect(() => {
        const loadKakaoMap = () => {
            if (!window.kakao) {
                console.error("Kakao Maps SDK not loaded");
                return;
            }

            const container = document.getElementById('map');
            const options = {
                center: new window.kakao.maps.LatLng(37.2640901, 126.9977417), // 좌표 설정
                level: 5, // 지도 확대 수준
            };

            const map = new window.kakao.maps.Map(container, options);
            const markerPosition = new window.kakao.maps.LatLng(37.2640901, 126.9977417); // 마커 위치 설정
            const marker = new window.kakao.maps.Marker({
                position: markerPosition,
            });
            marker.setMap(map); // 마커를 지도에 추가
        };

        const script = document.createElement('script');
        script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=7c83ad9e898fc41e0bf2bffb14232074&autoload=false`;
        script.async = true;
        script.onload = () => {
            window.kakao.maps.load(loadKakaoMap);
        };
        script.onerror = () => {
            console.error("Failed to load the Kakao Maps SDK");
        };
        document.head.appendChild(script);

        return () => {
            document.head.removeChild(script);
        };
    }, []);

    return (
        <div>
            <p className="location-title">LOCATION</p>
            <p className="location-subtitle">경기도 수원시 팔달구 효원로 25 MARRYVILIA THE PRESTIGE</p> {/* 주소 수정 */}
            <div className="map-container" id="map"></div>

            <div className="directions">
                <div className="transportation">

                    <p>지하철</p>
                    <p>수원역 1호선 3번 출구</p>

                    <p>버스</p>
                    <p>수원역 환승센터 (1승강장)하차, 평동동남아파트 하차</p>

                    <p>주차</p>
                    <p>경기도 수원시 권선구 세화로 116 서둔동 389</p><br></br>
                    <p>또는 메리빌리아 더 프리스티지 웨딩홀</p>
                </div>
            </div>
        </div>
    );
};

export default KakaoMap;
